// 引入阿里图库
import Icon from "../../static/iconfont/iconfont"
import "../../static/css/public.css"
import "../../static/iconfont/iconfont.css"
Vue.use(Icon)
//引入VUE
import VueRouter from 'vue-router'
import Vue from "vue"

import  Vuex from "vuex"
// / 数据请求
import Axios from 'axios'
Vue.prototype.$ajax = Axios
Vue.prototype.dataURL = function (file,title,id) {
  id = (id === undefined)?'':id;
  return file+'?title='+title+id;
}
//mint-ui组件
import { Swipe, SwipeItem ,Button} from 'mint-ui'
import { Header } from 'mint-ui'
import 'mint-ui/lib/style.css'
import { TabContainer, TabContainerItem,Cell } from 'mint-ui';

Vue.component(TabContainer.name, TabContainer);
Vue.component(Cell.name, Cell);
Vue.component(TabContainerItem.name, TabContainerItem);
Vue.component(Swipe.name, Swipe);
Vue.component(SwipeItem.name, SwipeItem);
Vue.component(Header.name, Header);
Vue.component(Button.name, Button);
//引入muse-ui组件

import MuseUI from 'muse-ui';
import 'muse-ui/dist/muse-ui.css';

// Vue.use(MuseUI);
//引入v-loadlazy组件
import LazyLoad from '../../static/lazyload'
Vue.use(LazyLoad);
//vue-router组件
import Home from '../components/home/home'
import HeadNav from "../components/headNav"
import Head from '../components/Header'
import Footer from '../components/footer/Footer'
import tanzhou from "../components/footer/tanzhou"
import weather from "../components/footer/weather"
import shopcart from "../components/footer/shop/shopcart"
import shopList from "../components/footer/shop/shopList"
import leaveMessage from "../components/home/leaveMessage/leaveMessage"
import news from "../components/home/news/news"

import note from "../components/home/note/note"
import phone from "../components/home/phone/phone"
import product from "../components/home/product/product"
import sharing from "../components/home/sharing/sharing"
import Myclass from "../components/footer/Myclass"
// news路由
import NBA from "../components/home/news/NBA/NBA"
import newlist from "../components/home/news/newlist"
import CBA from "../components/home/news/CBA/CBA"
import bbc from "../components/home/news/bbc/bbc"
import soccer from "../components/home/news/soccer/soccer"
import gg from "../components/home/news/gg/gg"
//jQ
import $ from "jquery"
Vue.prototype.$=$;
// 安装全局组件
Vue.component('HeadNav',HeadNav)
Vue.component('Head',Head)

Vue.use(VueRouter)
Vue.use(Vuex)
 Vue.use(MuseUI)

export default  new VueRouter({
   routes:[
     //底部路由
     {path:'/tanzhou',redirect:"/weather"},
     {path:'/',name:"shouye",component:Home},
     {path:'/header',component:Head},
     {path:'/footer',component:Footer},
     {path:'/tanzhou',name:"tanzhou",  component:tanzhou},
     {path:'/weather',name:"weather",  component:weather},
     {path:'/shopcart',name:"shopcart",  component:shopcart},
     {path:'/shopList',name:"shopList",  component:shopList,meta: {
         pageTitle: '商品详情',
         keepAlive: false,
       }},
     {path:'/Myclass',name:"Myclass",  component:Myclass},
     //中间路由
     {path:'/leaveMessage',name:"leaveMessage",  component:leaveMessage},
     {path:'/news',name:"news",  component:news,meta: {
         pageTitle: 'new主页',
         keepAlive: true,
       }},
     //新闻路径
     {path:'/NBA',name:"NBA",  component:NBA,meta: {
         pageTitle: 'NBA主页',
         keepAlive: true,
       }
     },
     {path:'/newlist',name:"newlist",  component:newlist,meta:{
       pageTitle: 'new详情页',
         keepAlive: false,

       }
     },
     {path:'/CBA',name:"CBA",  component:CBA,meta: {
         pageTitle: 'CBA主页',
         keepAlive: true,

       }},
     {path:'/soccer',name:"soccer",  component:soccer},
     {path:'/gg',name:"gg",  component:gg},
     {path:'/bbc',name:"bbc",  component:bbc},

     {path:'/note',name:"note",  component:note},
     {path:'/phone',name:"phone",  component:phone},
     {path:'/product',name:"product",  component:product,meta: {
         keepAlive: true
       }},

     {path:'/sharing',name:"sharing",  component:sharing},
     {path:'*',redirect:"/"},


   ],

  mode: 'history'
})





