<script>
  import {navlist, matchBoard} from "../../../../../static/info/info"

  export default {
    name: "NBA",
    data() {
      return {
        datalist: "",
        banner: {},
        navlist,
        matchBoard,
        active2: 0
      }
    },

//修改列表页的meta值，false时再次进入页面会重新请求数据。

    created() {

      var _this = this
      this.$ajax({
        method: 'get',
        url: '../static/newlist.json'
      }).then((res) => {
         // console.log(res)
        _this.datalist = res.data.NBAnew
        _this.banner = res.data.NBAbanner
      }).catch(function (err) {
        console.log(err);
      });

    },

  }
</script>

