<template>
    <div id="nav" >
      <mt-header :title="title" class="back">
        <a  slot="left" @click="goBack">
          <mt-button icon="back" >返回</mt-button>
        </a>
      </mt-header>
    </div>
</template>

<script>
    export default {
      props:[
        "title"
      ],
        name: "headerNav",
        data() {
            return {}
        },
      methods:{
        goBack(){
          this.$router.go(-1)
        }
      }
    }
</script>

<style scoped>
 #nav{
   line-height:10rem;
 }
  .back{
    background-color: rgba(0,0,0,.3);
  }
</style>



