<template>
  <div id="home" class="high bgc1d">
    <Head/>
    <div class="banner">
      <mt-swipe :auto="4000" >
        <mt-swipe-item class="banner">

          <img src="https://img.alicdn.com/tfs/TB1goWVI3HqK1RjSZFEXXcGMXXa-520-280.jpg" width="100%" height="100%" alt="">

        </mt-swipe-item>
        <mt-swipe-item class="banner">

          <img src="https://aecpm.alicdn.com/tfscom/TB10dPGH6TpK1RjSZKPXXa3UpXa.jpg" width="100%" height="100%" alt="">

        </mt-swipe-item>
        <mt-swipe-item class="banner">

          <img src="https://img.alicdn.com/simba/img/TB1mzYlJmrqK1RjSZK9SutyypXa.jpg" width="100%" height="100%" alt="">

        </mt-swipe-item>
      </mt-swipe>
    </div>

    <!--九宫格-->
    <div class="grids clearFix">
      <router-link :to="{name:'news'}">
        <div class="center">
          <svg class="icon">
            <use xlink:href="#icon-xinwenzixun-gai"></use>
          </svg>
          <p class="weui_grid_label">
            新闻资讯
          </p>
        </div>
      </router-link>
      <router-link :to="{name:'sharing'}">
        <div class="center">
          <svg class="icon">
            <use xlink:href="#icon-icon_graphic"></use>
          </svg>
          <p class="weui_grid_label">
            图文分享
          </p>
        </div>
      </router-link>
      <router-link :to="{name:'product'}">
        <div class="center">
          <svg class="icon">
            <use xlink:href="#icon-weibiaoti-"></use>
          </svg>
          <p class="weui_grid_label">
            商品展示
          </p>
        </div>
      </router-link>
      <router-link :to="{name:'leaveMessage'}">
        <div class="center">
          <svg class="icon" aria-hidden="true">
            <use xlink:href="#icon-liuyan"></use>
          </svg>
          <p class="weui_grid_label">
            留言反馈
          </p>
        </div>
      </router-link>
      <router-link  :to="{name:'note'}" class="weui_grid js_grid">
        <div class="center">
          <svg class="icon" aria-hidden="true">
            <use xlink:href="#icon-duanxin"></use>
          </svg>
          <p class="weui_grid_label">
            短信联系

          </p>
        </div>
      </router-link>
      <router-link :to="{name:'phone'}" class="weui_grid js_grid">
        <div class="center">
          <svg class="icon" aria-hidden="true">
            <use xlink:href="#icon-dianhua"></use>
          </svg>
          <p class="weui_grid_label">
            电话联系
          </p>
        </div>
      </router-link>

    </div>

  </div>
</template>

<script>

  export default {
    name: "home",
    data() {
      return {}
    },



  }
</script>

<style scoped lang="less">
  @rem: 750/10rem;
  .high {

    .banner {
      height: 28vh;
      background: #ddd;
    }

    .icon {
      /* 通过设置 font-size 来改变图标大小 */
      width: 1em;
      height: 1em;
      /* 图标和文字相邻时，垂直对齐 */
      vertical-align: -0.15em;
      /* 通过设置 color 来改变 SVG 的颜色/fill */
      fill: currentColor;
      /* path 和 stroke 溢出 viewBox 部分在 IE 下会显示
         normalize.css 中也包含这行 */
      overflow: hidden;
      font-size: 42px;
      line-height: 100px;
      margin: 10px 12px 0;
      color: #333;
      text-align: center;
    }
    .grids {
      background: white;
      position: relative;
      height:60vh;

      a {
        position: relative;
        float: left;
        padding: 20px 10px;
        background-color:rgba(225,33,5,.5);
        width: 33.33333333%;
        height: 50%;
        -webkit-box-sizing: border-box;
        box-sizing: border-box;
        display: flex;
        justify-content: center;
        align-items: center;
      }
      a::before {
        content: '';
        position: absolute;
        -webkit-box-sizing: border-box;
        box-sizing: border-box;
        width: 200%;
        height: 200%;
        left: 0;
        top: 0;
        border-bottom: 1px solid #d9d9d9;
        border-right: 1px solid #d9d9d9;
        -webkit-transform-origin: 0 0;
        transform-origin: 0 0;
        -webkit-transform: scale(.5);
        transform: scale(.5);
      }
      a:nth-child(3n):before {
        border-right-width: 0;
      }
      a p {
        display: block;
        text-align: center;
        color: #000;
        font-size: 28/@rem;
      }
      .center {
        margin: auto;
        .weui_grid_label{
          font-size:80/@rem;
          margin-top:6px;
        }
      }
    }
  }
</style>




