<template>
  <footer class="wrap w100 fld pa-b0 ">
    <ul class="flx2  plt5 prt5">
      <!--<li><router-link tag="div" to="/" class="nav-link">主页</router-link></li> tag改变router-link标签样式-->
      <!--<li><router-link :to="home" class="nav-link">主页</router-link></li>-->
      <li>
        <router-link to="/" class="navbar-brand text-primary">
          <i class="d-b t-c iconfont icon-dibudaohanglan_fangzi_tiaozhuanzhuye"></i>
          主页
        </router-link>
      </li>
      <li>
        <router-link :to="{name:'tanzhou'}" class="nav-link">
          <i class="d-b t-c iconfont icon-xuexiao" ></i>
          地图
        </router-link>
      </li>
      <li>
        <router-link to="weather" class="nav-link">
          <i class="d-b t-c iconfont icon-tianqi"></i>
          天气
        </router-link>
      </li>
      <li>
        <router-link :to="{name:'shopcart'}" class="nav-link">
          <i class="d-b t-c iconfont icon-gouwu">
            <i class="showNum" v-show="show&&goddsNumber&&goddsNumber>0">{{goddsNumber}}</i>
          </i>
          购物
        </router-link>
      </li>
      <li>
        <router-link :to="{name:'Myclass'}"  class="nav-link ">
          <i class="d-b t-c iconfont icon-kecheng"></i>
          我的课程
        </router-link>
      </li>
    </ul>
  </footer>
</template>

<script>
  import bus from '../../template/bus'
  import {mapGetters} from 'vuex'
  export default {
    name: "Footer",

    data() {
      return {
        show:false
      }
    },

    created(){
      const _this=this
      bus.$on("showbool",function (data) {
        if(data){
          _this.show=true
        }else {
          _this.show=false
        }

      })

    },
    computed:{
      ...mapGetters(
        [ "goddsNumber"]
      )
      // =>>等同于下面
      // goddsNumber(){
      //   return this.$store.getters.goddsNumber
      // }
    },


  }
</script>

<style scoped lang="less">
  @rem:750/10rem;
  .wrap {
    background-color: #ff7121;
    font-size: 80/@rem;
    color:#0086b3;
    ul{

      line-height:4vh;
      li{
        a{
          color:#0086b3;
          font-weight: 400;
        }
      }
    }
    i{
      font-size:99/@rem;
      color:#0086b3;
      .showNum{
        position: absolute;
        background-color:brown;
        width:20px;
        height:20px;
        font-size:12/@rem;
        color:#f5f2f0;
        vertical-align:middle;
        line-height: 20px;
        -webkit-border-radius: 50%;
        -moz-border-radius: 50%;
        border-radius: 50%;
      }
    }
  }

</style>



