<template>
  <div id="product">
    <Head-nav title="商店购物"></Head-nav>
    <section class="likeYou " >
      <ul id="likeYouList" class="aui-refresh-content">
        <li v-for="(item,index) in shopList" :key="index">
          <router-link :to="{name:'shopList',query:{id:index,title:'likeYou'}}" :title="item.des" >
            <div class="pic">
              <img  width="100%" height="100%"
                    src="https://gju2.alicdn.com/tps/i4/TB2VcukX4TpK1RjSZFMXXbG_VXa_!!0-juitemmedia.jpg_400x400Q90.jpg_.webp" v-lazyload="item.imgUrl" ></div>
            <p class="des" v-text="item.des"></p>
            <div class="shopPrice clearFix">
              <div class="left fl"><span class="symbol" v-text="item.symbol"></span><span class="price" v-text="item.price"></span></div>
              <div class="alreadyPaid fr" v-text="item.alreadyPaid"></div>
            </div>
          </router-link>
        </li>
      </ul>
    </section>

  </div>
</template>

<script>
  import bus from '../../../template/bus'
  export default {
    name: "product",
    data() {
      return {
        shopList:{},
        bol:true,
        brl:false
      }
    },
    beforeRouteEnter(to, from, next){

      next(vm=>{
        if (to.name==="product") {
          bus.$emit("showbool" ,vm.bol)
        }
      })
    },
    beforeRouteLeave(to,from,next){
      // console.log(to)
      if(to.name!=="shopList"){
        bus.$emit("showbool" ,this.brl)
      }
      next()
    },

    created(){

      const _this=this
      this.$ajax.get("/static/shop.json").then((res)=>{
        // console.log(res.data)
        _this.shopList=res.data.shopList
      })

    }

  }
</script>

<style scoped lang="less">
  @rem:750/10rem;
  .mb{
    margin-bottom: 130/@rem;
  }
  /*likeYou*/
  .likeYou{
    overflow: hidden;
    position: relative;
    padding-top: 30/@rem;
    background: #eee;
    ul{
      display: -webkit-flex;
      display: flex;
      justify-content: space-between;
      flex-wrap: wrap;
    }
    ul li{
      width: 49vw;
      height: 35vh;
      background: white;
      font-size: 27/@rem !important;
      overflow: hidden;
      margin-bottom: 5px;
    }
    ul li a{
      display: block;

    }
    ul li .pic{
      height: 25vh;
    }

    ul li .des{
      font-size: 27/@rem !important;
      padding:0 5px;
      margin: 3px 0;
      height: 3vh;
      overflow: hidden;
      -ms-text-overflow:ellipsis;
      -ms-text-overflow: ellipsis;
      -o-text-overflow: ellipsis;
      text-overflow:ellipsis ;
      white-space:nowrap;
      max-height: 30vh;
      -webkit-line-clamp: 2;
      word-break: break-all;
      line-height: 3vh;
      vertical-align: middle;
      text-align: center;
      color: rgb(51, 51, 51);
    }

    ul li .shopPrice{
      padding: 5px;
      padding-bottom: 0;
    }
    ul li .left{
      color: #ff5000;
    }
    ul li .left span{
      vertical-align: bottom;
    }
    ul li .symbol{
      font-size: 10/@rem;
    }
    ul li .price{
    }
    ul li .alreadyPaid{
      color: rgb(153, 153, 153);;
    }
  }
</style>



