<template>
    <div>
      <Head-nav title="CBA世界"></Head-nav>
      <mu-container>
        <mu-tabs :value.sync="active2" color="green" indicator-color="yellow" full-width>
          <mu-tab  :disabled="true" >CBA</mu-tab>
          <mu-tab>新闻</mu-tab>
          <mu-tab>社区</mu-tab>
        </mu-tabs>
        <div class="demo-text" ></div>
        <div class="demo-text" v-if="active2 === 1">
          <div class="common-list news-list" id="J_content" data-url="//m.hupu.com/nba/news/" data-pagecount="12">
            <ul>
              <li v-for="(item,index) in CBAlist" :key="index">
                <router-link :to="{name:'newlist',query:{id:item.id,
                                                      src:item.src,
                                                      title:item.title,
                                                      time:item.time,
                                                      from:item.from,}}" class="news-link" dace-node="2110_readNews">
                  <div class="news-wrap">
                    <div class="img-wrap"
                         :style="{backgroundImage:'url('+item.src+')'}"></div>
                    <div class="news-txt">
                      <h3>{{item.title}}</h3>
                      <div class="news-status-bar">
                        <div class="news-info">
                          <span class="news-source">{{item.from}}</span>
                          <span class="news-time">{{item.time}}</span>
                        </div>
                      </div>
                    </div>
                  </div>
                </router-link>
              </li>
            </ul>
          </div>
          <div class="kong" style="height:60px"></div>
        </div>
        <div class="demo-text" v-if="active2 === 2"></div>
      </mu-container>
    </div>
</template>
<script>
    import { Url, CBAlist} from "../../../../../static/info/CBAId"

    export default {
      name: "NBA",
      data() {
        return {
          CBAlist,
          Url,
          active2: 1
        }
      },
      methods:{
        hint(){
          alert(1)
        }
      },

      created() {

        // this.$ajax({
        //   method: 'get',
        //   url: '../static/newlist.json'
        // }).then((res) => {
        //   console.log(res)
        //   _this.datalist = res.data.NBAnew
        //   _this.banner = res.data.NBAbanner
        // }).catch(function (err) {
        //   console.log(err);
        // });

      }

    }
</script>

<style lang="less">
  .container{
    padding:0;
    .demo-text {
      background: #fff;
    }
  }
  .baner {
    .banner {
      height: 13rem;
      background: #ddd;
      .text {
        position: relative;
        bottom: 35px;
        text-align: center;
        line-height: 30px;
        font-weight: bold;
        color: #ffffff;
        background-color: rgba(0, 0, 0, .5);
      }

    }
  }

  //比赛赛况
  .match-board {
    padding: 0;
    background-color: #f3f3f3;
    li {
      border-bottom: 1px solid #e3e4e5;
      padding: 5px 0;
      line-height: 40px;
    }
    .game-link {
      display: block;
      display: -webkit-box;
      display: -ms-flexbox;
      display: -webkit-flex;
      display: -moz-flex;
      display: flex;
      .game-status {
        color: #444;
        line-height: 30px;
        width: 15%;
        text-align: center;
        font-size: 12px;
        /*line-height: 14px;*/
      }
      .game-info {
        -webkit-box-flex: 1;
        -ms-flex: 1;
        -webkit-flex: 1;
        -moz-flex: 1;
        flex: 1;
        display: -webkit-box;
        display: -ms-flexbox;
        display: -webkit-flex;
        display: -moz-flex;
        display: flex;
        padding: 0 5px;
        border-right: 1px solid #e3e4e5;
        .result {
          font-size: 14px;
          line-height: 30px;
          color: #444;
        }
        .team {
          -webkit-box-flex: 1;
          -ms-flex: 1;
          -webkit-flex: 1;
          -moz-flex: 1;
          flex: 1;
          font-size: 14px;
          line-height: 30px;
          color: #444;
        }
        .league {
          height: 16px;
          text-align: center;
          font-size: 12px;
          line-height: 16px;
          margin-top: 6px;
          margin-right: 5px;
          padding: 0 2px;
        }
        .league.basketball {
          color: #fd521d;
          border: 1px solid #fd521d;
        }
      }
    }

  }

  /*新闻详情*/
  .text-list {
    padding: 10px 5%;
    dd {
      border-bottom: 1px solid #f6f6f6;
      padding-bottom: 20px;
      position: relative;
      min-height: 50px;
      margin-top: 10px;
      font-size: 18px;
      h2 {
        font-size: 16px;
        line-height: 27px;
      }
      span {
        position: absolute;
        font-size: 12px;
        color: #a3a3a3;
        right: 0;
        bottom: 5px;
        line-height: 12px;
      }
      a {
        position: absolute;
        width: 100%;
        height: 100%;
        left: 0;
        top: 0;
      }
    }
  }

  /*新闻版块*/
  .common-list {
    li {
      a {
        display: block;
        padding: 10px 10px 0;
        -webkit-tap-highlight-color: rgba(200, 12, 0, .1);
        position: relative;
        .news-wrap {
          display: block;
          display: -moz-box;
          display: -webkit-box;
          display: box;
          .img-wrap {
            width: 72px;
            height: 72px;
            overflow: hidden;
            margin-right: 10px;
            text-align: center;
            background-clip: padding-box;
            background-size: cover;
            background-position: 50% 50%;
            background-repeat: no-repeat;
          }
          .news-txt {
            display: block;
            -moz-box-flex: 1;
            -webkit-box-flex: 1;
            box-flex: 1;
            h3 {
              height: 52px;
              font-size: 18px;
              line-height: 1.5;
              color: #444;
              overflow: hidden;
            }
            .news-status-bar {
              margin-top: 2px;
              display: -webkit-box;
              display: -ms-flexbox;
              display: -webkit-flex;
              display: -moz-flex;
              display: flex;
              color: #999;
              .news-info {
                -webkit-box-flex: 1;
                -ms-flex: 1;
                -webkit-flex: 1;
                -moz-flex: 1;
                flex: 1;
              }
              .news-source {
                max-width: 100px;
                height: 21px;
                white-space: nowrap;
                overflow: hidden;
                vertical-align: bottom;
              }
              .news-time {
                margin-left: 6px;
              }
            }
          }
        }
      }
    }
  }
  //比赛赛程
  .match-list-wrap {
    background-color: #f1f1f1;
    padding: 0 10px 6px;
  }
  .match-section:first-child {
    padding-top: 6px;
  }
  .match-section {
    a {
      background: transparent;
      text-decoration: none;
      color: #444;
    }
    .time-label {
      text-align: center;
      span {
        display: inline-block;
        height: 30px;
        background-color: #f8f8f8;
        padding: 0 18px;
        border-radius: 15px;
        position: relative;
        margin-bottom: 6px;
        strong {
          font-size: 14px;
          line-height: 30px;
          color: #444;
        }
      }
    }
    .match-wrap {
      *, :before, :after {
        -webkit-box-sizing: border-box;
        -moz-box-sizing: border-box;
        /* box-sizing: border-box; */
      }
      display: block;
      margin-bottom: 6px;
      img {
        border: 0;
        vertical-align: middle;
      }
      .match-info {
        background-color: #FFF;
        padding: 15px 0 10px;
        font-size: 16px;
        clear:both;
        .match-team-info {
          margin-bottom: 8px;
          clear:both;
          .team img {
            width: 28px;
          }
          .team {
            float: left;
            width: 35%;
            display: -moz-box;
            display: -webkit-box;
            display: box;
          }
          .team.away-team {
            text-align: right;
            .team-name {
              display: block;
              -moz-box-flex: 1;
              -webkit-box-flex: 1;
              box-flex: 1;
            }
            img {
              margin-left: 8px;
            }
          }
          strong {
            float: left;
            width: 30%;
            text-align: center;
          }
          .team.home-team {
            text-align: left;
            img {
              margin-right: 8px;
            }
          }
        }
        .match-status-info {
          font-size: 12px;
          color: #444;
          .score {
            float: left;
            width: 25%;
            line-height: 14px;
            text-align: center;
            height: 16px;
          }
          .score.away-score {
            margin-left: 10%;
          }
          .match-status-txt {
            float: left;
            width: 30%;
            height: 14px;
            text-align: center;
            position: relative;
          }
          .score.home-score {
            margin-right: 10%;
          }
        }
      }
    }
  }
  .match-section .match-team-info:after, .match-section .match-status-info:after {
    clear: both;
  }
  .match-section .match-team-info:before, .match-section .match-status-info:before, .match-section .match-team-info:after, .match-section .match-status-info:after {
    content: "";
    display: table;
  }
</style>



