
import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex);

export const store= new Vuex.Store({
  state:{
    goodsList:localStorage["goodsList"]?JSON.parse(localStorage["goodsList"]): [],
    changableNum:0
  },

  getters:{
    sum:state=>{
      var total=0;
      state.goodsList.forEach((item)=>{
        if(item.select){
          total+=item.price*item.buynum
        }
      })
      return total
    },
    //购物车要显示的总数
    goddsNumber:state=>{
      var allNum=0;
      state.goodsList.forEach((item)=>{
        //select真假真数量加上
        if(item.select){
          allNum+=item.buynum
        }
      })
      return allNum
    },

    getChangedNum:state=> {  //承载变化的changebleNum的值
     return  state.changableNum
   }
  },
//增删改查
  mutations:{
    addGoods:(state,data)=>{
      state.goodsList.push(data);
      localStorage.setItem("goodsList",JSON.stringify(state.goodsList));
    },
    deleteGoods(state,index){
      // delete时：获取index位置后通过splice（index,1）;
      state.goodsList.splice(index,1);
      localStorage.setItem("goodsList",JSON.stringify(state.goodsList));
    },
    // <!--index为操作第几个元素，key为要改变的key,value为新的值 -->
    updateGoods(state,data){

      const {index,key,value}=data;
      state.goodsList[index][key]=value;
      localStorage.setItem("goodsList",JSON.stringify(state.goodsList));
    },
    newNum(state, sum) {
     state.changableNum += sum;
   }
  },
  actions :{

  getNewNum(context,num){   //同上注释，num为要变化的形参
    context.commit('newNum',num)
  }
}
})



