<template>
  <div id="newlist">
    <Head-nav title="NBA新闻详情"></Head-nav>
    <div class="detail-content">
      <header class="artical-title">
        <h1 class="headline" v-text="title"></h1>
        <div class="artical-info">
              <span class="author-name">
                  <span class="media" v-text="from"></span>
              </span>
          <span class="times" v-text="time"></span>
        </div>

      </header>
      <article class="article-content">
        <img :src="src">
        <p v-for="(item,index) in text" :key="index" v-html="item">
          {{item}}
        </p>
      </article>
    </div>
    <!--<div class="text" v-html="datalist">{{datalist}}</div>-->
  </div>
</template>

<script>
  export default {
    name: "newlist",
    data() {
      return {
        src:"",
        title:"",
        text:"",
        from:"",
        time:"",
      }
    },
    beforeRouteLeave(to, from, next) {
// console.log(to)
// console.log(from)
      if (to.path == "/NBA"||to.path == "/CBA") {
        to.meta.keepAlive = true;
      } else {
        to.meta.keepAlive = false;
      }
      next();
    },
    created() {
      // console.log(this.$route.query);
      const data=this.$route.query
      this.src=data.src;
      this.title=data.title;
      this.time=data.time;
      this.from=data.from;
      this.text=data.text;

      const _this=this;
      const i=data.id
      this.$ajax({
        method: 'get',
        url: '../static/CBAlist.json'
      }).then((res) => {
        if (!_this.text){
          _this.text=res.data.CBAtext[i].text
        }

      }).catch(function (err) {
        console.log(err);
      });

    }
  }
</script>

<style scoped lang="less">
  *, :before, :after {
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
  }
  html, button, input, select, textarea {
    font-family: "Helvetica Neue",Helvetica,"Heiti SC",sans-serif;}
  body {
    margin: 0;
    font-size: 14px;
    line-height: 1.5;
    color: #444;
    background-color: #fff;
  }
  .detail-content {
    padding-bottom: 15px;
    background: #fff;
    border-bottom: 1px solid #e3e4e5;
    margin-bottom: 10px;

    .artical-title {
      padding: 15px 10px 10px;
      .headline {
        padding-bottom: 10px;
        font-size: 20px;
        line-height: 1.4;
        font-weight: 700;
        text-align: left;
      }
      .artical-info {
        display: -webkit-box;
        display: -ms-flexbox;
        display: -webkit-flex;
        display: -moz-flex;
        display: flex;
        color: #999;
        .times {
          margin: 0 5px;
        }
      }
    }
    .article-content {
      padding: 0 10px 10px;
      word-wrap: break-word;
      font-size: 18px;
      img {
        display: block;
        max-width: 100%;
        margin: 0 auto;
      }
      p {
        padding: 10px 0;
        img {
          display: block;
          max-width: 100%;
          margin: 0 auto;
        }
      }
      a {
        color: #005eac;
      }
    }
  }


</style>



