<template>
  <div class="mb ">
    <head-nav title="商品详情"></head-nav>
    <!--<div class="alert fs18 ab cf z99" v-show="show">-->
      <!--<center>已经选了这个呀!!</center>-->
    <!--</div>-->
    <div class="shopdedatils">
      <div class="img" :style="{backgroundImage:'url('+newsDetails.imgUrl+')'}"></div>

      <h3 v-text="newsDetails.des"></h3>
      <p class="money">
        <span class="symbol" v-text="newsDetails.symbol"></span>
        <span class="price" v-text="newsDetails.price"></span>
      </p>
      <p class="courier">快递：包邮 <span class="fr" v-text="newsDetails.alreadyPaid"></span></p>
      <div class="buyNum clearFix">
        <p class="fl buyfont">购买数量</p>
        <p class="addNum fr">
          <button href="javascript:;" class="reduce" @click="dec" :disabled="bool">-</button>
          <a href="javascript:;" class="num">{{addnum}}</a>
          <button href="javascript:;" class="add" @click="add" :disabled="ll">+</button>
        </p>
      </div>
      <div class="buy">
        <a href="javascript:;" class="addCart" @click="addToCar(id)">加入购物车</a>
        <router-link :to="{name:'shopcart',query:{title:'likeYou'}}" class="nowBuy">立即购买</router-link>
      </div>
    </div>
  </div>
</template>

<script>
  import bus from '../../../template/bus'

  export default {
    name: "shopList",
    data() {
      return {
        newsDetails: {},
        num: 0,
        index: 0,
        bool: false,
        ll: false,
        bol: true,
        show: false
      }
    },
    //用于只有点击商品相关的页面显示下面购物车数量
    beforeRouteEnter(to, from, next) {
      next(vm => {
        if (to.name === "shopList") {
          bus.$emit("showbool", vm.bol)
        }
      })
    },
    beforeRouteLeave(to, from, next) {

      if (to.name !== "shopcart" || to.name !== "product") {
        bus.$emit("showbool", this.brl)
      }
      next()
    },
    computed: {
      //id
      id() {
        return this.$route.query.id
      },

      addnum() {
        const numlist = this.$store.getters.getChangedNum
        return numlist;
      }
    },
    created() {

      const _this = this
      const i = this.$route.query.id

      this.$ajax.get("/static/shop.json").then((res) => {
        // console.log(res.data.shopList[i])
        _this.newsDetails = res.data.shopList[i]
      })
    },
    methods: {
      // 增加数量
      add() {

        if (20 > this.$store.getters.getChangedNum >= 0) {
          this.bool = false
        }
        if (this.$store.getters.getChangedNum === 19) {
          this.ll = true;
          alert("抱歉,最多只能买这么多,再多请联系客服")
        }

        return this.$store.dispatch('getNewNum', 1)
      },
      //减少
      dec() {


        if (this.$store.getters.getChangedNum == 1) {
          this.bool = true
        }
        if (this.$store.getters.getChangedNum <= 20) {
          this.ll = false
        }

        return this.$store.dispatch('getNewNum', -1)
      },
      //加入购物车
      addToCar(id) {
        var carList = this.$store.state.goodsList
        // 点击按钮时，首先判断该商品是否在购物车已存在，如果存在则不再加入。
        var idExist = carList.find(item => {
          return item.id == id
        })
        // 使用es6数组新增find函数来实现，该函数返回满足条件的第一个元素。
        // 如果不存在该id，
        // 则将对应的数据存入。
        if (!idExist) {
          var data = {
            id: this.id,
            imgUrl: this.newsDetails.imgUrl,
            des: this.newsDetails.des,
            symbol: this.newsDetails.symbol,
            price: this.newsDetails.price,
            alreadyPaid: this.newsDetails.alreadyPaid,
            buynum: this.addnum,
            select: true,
          }
          this.$store.commit('addGoods', data)
        } else {
          return alert('已加入购物车')
          // setTimeout(()=>{
          //   console.log(this)
          //   this.show=true
          // },2000)

        }

      }
    }
  }
</script>

<style scoped lang="less">
  @rem: 750/10rem;
  .kong {
    height: 4vh !important
  }

  .mb {
    margin-bottom: 130/@rem;
  }

  .shopdedatils {
    max-height: 85vh;
    padding: 22/@rem;
    position: relative;
    text-align: left;
    background: white;
    .img {
      width: 98%;
      height: 52vh;
      margin: auto;
      background-clip: padding-box;
      background-size: cover;
      background-position: 50% 50%;
      background-repeat: no-repeat;
    }
    h3 {
      font-size: 83/@rem;
      font-weight: 700;
    }
    .money {
      color: red;
      .symbol {
        font-size: 82/@rem;
      }
      .price {
        font-size: 82/@rem;
      }
    }
    .courier {
      font-size: 72/@rem;
      color: #999;
    }
    .buyNum {
      border: 1px solid #e7e7e7;
      border-left: none;
      border-right: none;
      padding: 20/@rem;
      font-weight: 700;
      .buyfont {
        font-size: 70/@rem;
      }
      .addNum {
        font-size: 70/@rem;
        button {

          width: 150/@rem;
          background: #f1f1f1;
          display: inline-block;
          border: none;
          color: #878787;
          text-align: center;
          line-height: 4vh;
          outline: none;
        }
        a {
          width: 100/@rem;
          background: #f1f1f1;
          display: inline-block;
          color: #878787;
          text-align: center;
          line-height: 4vh;
        }
        a:active {
          background: #ddd;
        }
      }
    }
    .buy {
      display: flex;
      height: 8vh;
      text-align: center;
      line-height: 8vh;
      a {
        flex: 1;
        color: white;
        font-size: 91/@rem;
      }
      .addCart {
        background: #ff9402;
      }
      .nowBuy {
        background: #ff5000;
      }
    }
  }
</style>



