<template>
  <div id="new">

    <Head/>
    <div class="nav re ">
      <ul class="nav-list">
        <li v-for="(item,index) in navlist" :key="index">
          <router-link :to="{name:item.path, query:{id:item.id}}">
            <a >{{item.name}}</a>
          </router-link>

        </li>
      </ul>
    </div>

    <div class="baner ">
      <mt-swipe :auto="4000" class="banner fs18" :show-indicators="false">
        <mt-swipe-item class="banner" v-for="(item,index) in banner" :key="index">
          <router-link :to="{name:'NBA'}"><img :src="item.src" width="100%" height="100%" alt=""></router-link>
          <p class="text ">{{item.text}}</p>
        </mt-swipe-item>
      </mt-swipe>
    </div>
    <div class="match-board re">
      <ul>
        <li class="end">
          <router-link :to="{name:'NBA',query:{id:'NBA'}}" class="game-link">
            <div class="game-info">
              <div class="league basketball">
                NBA
              </div>
              <div class="team">
                凯尔特人vs勇士
              </div>
              <div class="result">
                128 - <span style='color:#999'>95</span>
              </div>
            </div>
            <div class="game-status">
              <div class="status-info">
                <p></p>
                <p>已结束</p>
              </div>
            </div>
          </router-link>
        </li>
        <li class="end">
          <a href="" class="game-link">
            <div class="game-info">
              <div class="league basketball">
                NBA
              </div>
              <div class="team">
                火箭vs猛龙
              </div>
              <div class="result">
                107 - <span style='color:#999'>95</span>
              </div>
            </div>
            <div class="game-status">
              <div class="status-info">
                <p></p>
                <p>已结束</p>
              </div>
            </div>
          </a>
        </li>
        <li class="end">
          <a href="" class="game-link">
            <div class="game-info">
              <div class="league basketball">
                NBA
              </div>
              <div class="team">
                雷霆vs森林狼
              </div>
              <div class="result">
                <span style='color:#999'>120</span> - 131
              </div>
            </div>
            <div class="game-status">
              <div class="status-info">
                <p></p>
                <p>已结束</p>
              </div>
            </div>
          </a>
        </li>
      </ul>
    </div>
    <div class="home-diy ">
      <section class="card-col-3" dace-node="1000_operate" v-for="(item,index) in datalist" :key="index">
        <router-link :to="{name:'newlist',query:{id:item.id}}">
          <div class="pic">
            <img :src="url+item.src" alt="">
          </div>
          <div class="info-txt">
            <article class="title">
              <span class="tag">{{item.sign}}</span>{{item.title}}
            </article>
            <div class="other-info">
            </div>
          </div>
        </router-link>
      </section>
    </div>
  </div>

</template>

<script>

  import { banner, navlist,Url} from "../../../../static/info/info"

  export default {
    name: "news",
    data() {
      return {
        show: true,
        datalist:[],
        banner,
        navlist,
        url:Url.url
      }
    },
    beforeRouteLeave(to, from, next) {
      // console.log(from)
      // console.log(to)
     to.meta.keepAlive = false;

      next();
    },
    created() {
      // console.log(this.datalist);
      var _this=this
      this.$ajax({
        method: 'get',
        url: '../static/data.json'
      }).then((res)=>{
        // var obj = JSON.parse(res.data);

        _this.datalist=res.data.datalist
      }).catch(function (err) {
        console.log(err);
      });

    }
  }
</script>

<style scoped lang="less">
  .nav {
    width: 100%;
    background-color: red;
    z-index: 9;
    padding: 5px;
    font-size: 16px;
  }

  .nav-list {
    display: flex;
    justify-content: space-around;
    font-weight: bold;
    line-height: 30px;
    li a {
      color: #ffffff
    }
  }

  .baner {
    .banner {
      height: 13rem;
      background: #ddd;
      .text {
        position: relative;
        bottom: 35px;
        text-align: center;
        line-height: 30px;
        font-weight: bold;
        color: #ffffff;
        background-color: rgba(0, 0, 0, .5);
      }

    }
  }

  //比赛赛况
  .match-board {
    padding: 0;
    background-color: #f3f3f3;
    li {
      border-bottom: 1px solid #e3e4e5;
      padding: 5px 0;
      line-height: 40px;
    }
    .game-link {
      display: block;
      display: -webkit-box;
      display: -ms-flexbox;
      display: -webkit-flex;
      display: -moz-flex;
      display: flex;
      .game-status {
        color: #444;
        line-height: 30px;
        width: 15%;
        text-align: center;
        font-size: 12px;
        /*line-height: 14px;*/
      }
      .game-info {
        -webkit-box-flex: 1;
        -ms-flex: 1;
        -webkit-flex: 1;
        -moz-flex: 1;
        flex: 1;
        display: -webkit-box;
        display: -ms-flexbox;
        display: -webkit-flex;
        display: -moz-flex;
        display: flex;
        padding: 0 5px;
        border-right: 1px solid #e3e4e5;
        .result {
          font-size: 14px;
          line-height: 30px;
          color: #444;
        }
        .team {
          -webkit-box-flex: 1;
          -ms-flex: 1;
          -webkit-flex: 1;
          -moz-flex: 1;
          flex: 1;
          font-size: 14px;
          line-height: 30px;
          color: #444;
        }
        .league {
          height: 16px;
          text-align: center;
          font-size: 12px;
          line-height: 16px;
          margin-top: 6px;
          margin-right: 5px;
          padding: 0 2px;
        }
        .league.basketball {
          color: #fd521d;
          border: 1px solid #fd521d;
        }
      }
    }

  }

  //新闻短信
  .card-col-3 {
    display: -moz-box;
    display: -webkit-box;
    display: box;
    a {
      display: -moz-box;
      display: -webkit-box;
      display: box;
      width: 100%;
      color:#444;
      img {
        width: 72px;
        height: 72px;
        margin-right: 15px;
        overflow: hidden;
      }
      .info-txt {
        display: block;
        -moz-box-flex: 1;
        -webkit-box-flex: 1;
        .title {
          height: 52px;
          overflow: hidden;
          .tag {
            display: inline-block;
            height: 18px;
            padding: 0 5px;
            font-size: 14px;
            line-height: 18px;
            font-weight: 400;
            background: #999;
            color: #fff;
            margin-right: 5px;
          }
        }
        .other-info {
          display: -moz-box;
          display: -webkit-box;
          display: box;
          padding-top: 8px;
          font-size: 14px;
          line-height: 1;
          color: #999;
        }
      }
    }
  }

</style>



