<template>
  <div>
    <head-nav title="结算购物车"></head-nav>
    <div class="notshop fs18 fw-b lh60" v-show="noshop">
      <center>空空如也,快去选购吧!</center>
    </div>
    <div class="shopArea mb">
      <p class="location clearFix" v-if="location">
        <span class="fl">送到地点: {{location}}</span>
        <span class="fr">编辑地址</span>
      </p>
      <div class="shop " v-for="(item,index) in goodsList" :key="index">
        <div class="shopShow">
          <div class="pic ">
            <img :src="item.imgUrl" width="100%" height="100%" alt="">
          </div>
          <div class="des ">{{item.des}}</div>
        </div>

        <div class="buyNum ">
          <p class=" buyfont">{{item.symbol}} {{item.price}}</p>
          <p class="addNum ">
            <a href="javascript:;" class="reduce" @click="changeNumber(item.id,-1)">-</a>
            <a href="javascript:;" class="num" >{{item.buynum}}</a>
            <a href="javascript:;" class="add" @click="changeNumber(item.id,1)">+</a>
          </p>
          <label >
            <input type="checkbox" name="goods" :checked="item.select==true" @change="toggleSelect(item.id)">
            <span></span>
          </label>
          <a href="javascript:;" class="del" @click="del(item.id)">删除</a>
        </div>
      </div>
      <div class="totalPrice">
        <div class="total">
          <p class="totalMoney">
            <span class="font">总计:</span> <span>￥ {{sum}}</span>
          </p>
          <p class="preferential">
            总金额￥{{sum}} 优惠￥0.00
          </p>
        </div>
        <div class="goPayment">
          <span class="font">去结算</span><span class="num">({{goddsNumber}})</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  // vuex提供了mapMutations
  // 辅助函数将组件中的 methods 映射为 store.commit 调用,
  // 当有多个mutation需要使用时,使用mapMutations可以让代码更为简洁。
  // 除了mutaion有mapMutation外，state,getters和actions也都有辅助的map函数，
  // 可以使用Mutation，可以一次获取多个状态和方法
  import { mapMutations } from 'vuex'
  import { mapGetters } from 'vuex'
  import bus from '../../../template/bus'
  export default {
    data() {
      return {
        location:'',
        shopList:[],
        noshop:false,
        bol:true,
        brl:false
      }
    },
    beforeRouteLeave(to,from,next){
      // console.log(to)
      if(to.name!=="shopList"){
        bus.$emit("showbool" ,this.brl)
      }
      next()
    },
    beforeRouteEnter(to, from, next){

      next(vm=>{
        if (to.name==="shopcart") {
          bus.$emit("showbool" ,vm.bol)
        }
      })
    },
    computed:{
      ...mapGetters(
        [ "sum","goddsNumber"]
      ),
      goodsList(){
        return this.$store.state.goodsList
      }
    },
    created(){
      this.findlength()

    },
    methods:{
      ...mapMutations(
        ["deleteGoods","updateGoods"]
      ),
      //检测商品种类;长度,没有显示 : 提示购买
      findlength(){
        const length=this.goodsList.length
       if(!length){
         this.noshop=true
       }
      },

      findPosition(id){
        return this.goodsList.findIndex(item=>{
          return item.id==id
        })
      },
      // 点击加减号时，传入+1或者-1：
      changeNumber(id,val){
        var i=this.findPosition(id);
        var number=this.goodsList[i].buynum;
        this.updateGoods({
          index:i,
          key:"buynum",
          value:number+val<=0?1:number+val
        })
      },

      del(id){
        var i=this.findPosition(id);
        this.deleteGoods(i);
        this.findlength()
      },

      toggleSelect(id){
        var i=this.findPosition(id);
        var select=this.goodsList[i].select;
        this.updateGoods({
          index:i,
          key:"select",
          value:!select
        })

      }
    },

  }
</script>

<style scoped lang="less">
  @rem:750/10rem;
  .mb{
    margin-bottom: 130/@rem;
  }
  .shopArea{
    text-align: left;
    background: #fff;

    .location{
      font-size: 32/@rem;
      color: #ccc;
      padding: 10/@rem;
      padding: 20/@rem 55/@rem;
    }
    .shop{
      border-bottom: 1px solid #ddd;
      border-top: 1px solid #ddd;
      padding: 0 55/@rem;
      .shopShow{
        display: flex;
        justify-content: space-between;
        align-items: center;
        overflow: hidden;
        padding: 25/@rem 0;
        .pic{
          width: 25vw;
          height: 15vh;
        }
        .des{
          width: 400/@rem;
          font-size: 35/@rem;
        }
      }

      .buyNum{
        border-top: 1px solid #ddd;
        border-left: none;
        border-right: none;
        padding: 20/@rem 0;
        display: flex;
        justify-content: space-between;
        align-items: center;
        .buyfont{
          font-size: 40/@rem;
        }
        .addNum{
          font-size: 40/@rem;
          a{
            width: 6vw;
            height: 3vh;
            background: #f1f1f1;
            display: inline-block;
            color: #878787;
            text-align: center;
          }
          a:active{
            background: #ddd;
          }
        }
        .del{
          color: skyblue;
          font-size: 40/@rem;
        }
      }
    }

    .totalPrice{
      height: 4vh;
      line-height: 4vh;
      text-align: center;
      display: flex;
      justify-content: space-between;
      margin-top: 20/@rem;
      background: rgba(255,255,255,0.7);
      box-shadow: 0 -1px 2px #d7d7d7;
      /*align-items: center;*/
      .all{

        color: #999;
        font-size: 20/@rem;
        i{
          width: 40/@rem;
          height: 40/@rem;
          border-radius: 50%;
          background: #e93b3d;
        }
        i::after{
          content: '';
        }
      }
      .total{
        text-align: left;
        text-indent:8px;
        p{
          height: 50%;
          font-size: 32/@rem ;
          line-height: 50/@rem;
          color: #999;
          margin-top:5px
        }
        .totalMoney{
          color: #e4393c;
          font-weight: bold;
          .font{
            color: #333;
          }
        }
        .preferential{
          font-size: 18/@rem;
        }
      }
      .goPayment{
        padding:0 6px;
        background: #e4393c;
        color: #fff;
        span{
          vertical-align: middle;
        }
        .font{
          font-size: 32/@rem;
        }
        .num{
          font-size: 23/@rem;
        }

      }
    }
  }

</style>



